:root {
  --bs-header-dark-item-color: #e9ecef;
  --bs-header-dark-bg: #fff;
  --bs-topnav-bg: #0576b9;
  --bs-input-bg: #fff;
  --bs-input-border-color: #e9ebec;
  --bs-input-focus-border-color: #ced2d4;
  --bs-form-select-focus-border-color: #ced2d4;
  --bs-white: #fff;
}

.accordion {
  --bs-accordion-color: #212529;
}

#page-topbar {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1002;
  background-color: #ffffff;
  border-bottom: 1px solid rgba(233, 235, 236, 0.6);
}

.navbar-header {
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  height: 60px;
  padding: 0 calc(20px / 2) 0 0;
}
.navbar-header .dropdown .show.header-item {
  background-color: #f8f9fa;
}

.navbar-brand-box {
  padding: 0 1.5rem;
  width: 240px;
}

body:not([data-topbar=dark]) .navbar-brand-box {
  background-color: #ffffff;
}

.logo {
  line-height: 60px;
}
.logo .logo-sm {
  display: none;
}

.logo-light {
  display: none;
}

/* Search */
.app-search {
  padding: calc(22px / 2) 0;
}
.app-search .form-control {
  border: none;
  height: 38px;
  padding-left: 40px;
  padding-right: 20px;
  background-color: #f3f3f9;
  box-shadow: none;
}
.app-search span {
  position: absolute;
  z-index: 10;
  font-size: 16px;
  line-height: 38px;
  left: 13px;
  top: 0;
  color: #9599ad;
}

.megamenu-list li {
  position: relative;
  padding: 5px 0px;
}
.megamenu-list li a {
  color: #495057;
}

@media (max-width: 992px) {
  .navbar-brand-box {
    width: auto;
  }

  .logo span.logo-lg {
    display: none;
  }
  .logo span.logo-sm {
    display: inline-block;
  }
}
.page-content {
  padding: calc(60px + 20px) calc(20px / 2) 60px calc(20px / 2);
}

.header-item {
  height: 60px;
  box-shadow: none !important;
  color: #555b6d;
  border: 0;
  border-radius: 0px;
}
.header-item:hover {
  color: #555b6d;
}

.header-profile-user {
  height: 32px;
  width: 32px;
}

.noti-icon i {
  font-size: 22px;
  color: #555b6d;
}
.noti-icon .badge {
  position: absolute;
  top: 12px;
  right: 4px;
}

.notification-item {
  padding: 0.75rem 1rem;
}
.notification-item:hover {
  background-color: var(--bs-gray-300);
}

.dropdown-icon-item {
  display: block;
  border-radius: 3px;
  line-height: 34px;
  text-align: center;
  padding: 15px 0 9px;
  border: 1px solid transparent;
  color: #9599ad;
}
.dropdown-icon-item img {
  height: 24px;
}
.dropdown-icon-item span {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.dropdown-icon-item:hover {
  border-color: #eff2f7;
}

.fullscreen-enable [data-toggle=fullscreen] .mdi-fullscreen::before {
  content: "\f0294";
}

body[data-topbar=dark] #page-topbar {
  background-color: var(--bs-topnav-bg);
  border-color: var(--bs-border-color);
}
body[data-topbar=dark] .navbar-header .dropdown .show.header-item {
  background-color: rgba(255, 255, 255, 0.05);
}
body[data-topbar=dark] .navbar-header .waves-effect .waves-ripple {
  background: rgba(255, 255, 255, 0.4);
}
body[data-topbar=dark] .header-item {
  color: #fff;
}
body[data-topbar=dark] .header-item:hover {
  color: #fff;
}
body[data-topbar=dark] .header-profile-user {
  background-color: rgba(255, 255, 255, 0.25);
}
body[data-topbar=dark] .noti-icon i {
  color: var(--bs-header-dark-item-color);
}
body[data-topbar=dark] .logo-dark {
  display: none;
}
body[data-topbar=dark] .logo-light {
  display: block;
}
body[data-topbar=dark] .app-search .form-control {
  background-color: rgba(243, 243, 249, 0.07);
  color: var(--bs-white);
  border: none;
}
body[data-topbar=dark] .app-search span,
body[data-topbar=dark] .app-search input.form-control::-webkit-input-placeholder {
  color: var(--bs-gray-400);
}

body[data-sidebar=dark] .navbar-brand-box {
  background: #121e2d;
  border-bottom: 1px solid #192a3f;
}
body[data-sidebar=dark] .logo-dark {
  display: none;
}
body[data-sidebar=dark] .logo-light {
  display: block;
}

@media (max-width: 600px) {
  .navbar-header .dropdown {
    position: static;
  }
  .navbar-header .dropdown .dropdown-menu {
    left: 10px !important;
    right: 10px !important;
  }
}
@media (max-width: 380px) {
  .navbar-brand-box {
    display: none;
  }
}
body[data-layout=horizontal] .navbar-brand-box {
  width: auto;
}
body[data-layout=horizontal] .page-content {
  padding: calc(115px + 20px) calc(20px / 2) 60px calc(20px / 2);
}

@media (max-width: 991.98px) {
  body[data-layout=horizontal] .page-content {
    padding-top: calc(60px + 20px);
  }
}
.page-title-box {
  padding: 7px 20px;
  background-color: var(--bs-card-bg);
  border-bottom: 1px solid var(--bs-card-border-color) !important;
  margin: -19px -20px 20px -20px;
}
.page-title-box .breadcrumb {
  background-color: transparent;
  padding: 0;
  font-size: 13px;
}
.page-title-box .breadcrumb h4 {
  font-weight: 700;
  font-size: 16px !important;
}
.page-title-box .breadcrumb a {
  color: var(--prefixcard-bg);
}

body[data-layout=horizontal] .page-title-box {
  padding: 20px 0;
  background-color: transparent !important;
  border-bottom: none;
  margin: -19px 0 0 0;
}

.footer {
  bottom: 0;
  padding: 20px calc(20px / 2);
  position: absolute;
  right: 0;
  color: #74788d;
  left: 240px;
  height: 60px;
  background-color: #f2f2f5;
}
@media (max-width: 991.98px) {
  .footer {
    left: 0;
  }
}

body[data-sidebar-size=sm] .footer {
  left: 70px;
}
@media (max-width: 991.98px) {
  body[data-sidebar-size=sm] .footer {
    left: 0;
  }
}

body[data-layout=horizontal] .footer {
  left: 0 !important;
}

[data-layout-mode=dark] .footer {
  color: var(--bs-footer-color);
  background-color: var(--bs-footer-bg);
}

.right-bar {
  background-color: var(--bs-white);
  box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.06), 0 1px 0 0 rgba(0, 0, 0, 0.02);
  display: block;
  position: fixed;
  transition: all 200ms ease-out;
  width: 320px;
  z-index: 9999;
  float: right !important;
  right: -330px;
  top: 0;
  bottom: 0;
}
.right-bar .right-bar-toggle {
  background-color: #444c54;
  height: 24px;
  width: 24px;
  line-height: 24px;
  display: block;
  color: #eff2f7;
  text-align: center;
  border-radius: 50%;
}
.right-bar .right-bar-toggle:hover {
  background-color: #4b545c;
}

.rightbar-overlay {
  background-color: rgba(52, 58, 64, 0.55);
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: none;
  z-index: 9998;
  transition: all 0.2s ease-out;
}

.right-bar-enabled .right-bar {
  right: 0;
}
.right-bar-enabled .rightbar-overlay {
  display: block;
}

@media (max-width: 767.98px) {
  .right-bar {
    overflow: auto;
  }
  .right-bar .slimscroll-menu {
    height: auto !important;
  }
}
.metismenu {
  margin: 0;
}
.metismenu li {
  display: block;
  width: 100%;
}
.metismenu .mm-collapse {
  display: none;
}
.metismenu .mm-collapse:not(.mm-show) {
  display: none;
}
.metismenu .mm-collapse.mm-show {
  display: block;
}
.metismenu .mm-collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition-timing-function: ease;
  transition-duration: 0.35s;
  transition-property: height, visibility;
}

.vertical-menu {
  width: 240px;
  z-index: 1001;
  background: #ffffff;
  bottom: 0;
  margin-top: 0;
  position: fixed;
  top: 60px;
}

.main-content {
  margin-left: 240px;
  overflow: hidden;
}
.main-content .content {
  padding: 0 15px 10px 15px;
  margin-top: 60px;
}

#sidebar-menu {
  padding: 10px 0 30px 0;
}
#sidebar-menu .mm-active > .has-arrow:after {
  transform: rotate(90deg);
}
#sidebar-menu .has-arrow:after {
  content: "\ed0b";
  font-family: boxicons;
  display: block;
  float: right;
  transition: transform 0.2s;
  font-size: 9px;
  margin-top: 7px;
}
#sidebar-menu ul li a {
  display: block;
  padding: 0.6rem 1.5rem;
  color: #545a6d;
  position: relative;
  transition: all 0.4s;
}
#sidebar-menu ul li a i {
  display: inline-block;
  min-width: 1.6rem;
  font-size: 18px;
  line-height: 1.40625rem;
  vertical-align: middle;
  color: #545a6d;
  transition: all 0.4s;
}
#sidebar-menu ul li a span {
  vertical-align: middle;
}
#sidebar-menu ul li a:hover {
  color: #383c40;
}
#sidebar-menu ul li a:hover i {
  color: #383c40;
}
#sidebar-menu ul li .badge {
  margin-top: 4px;
}
#sidebar-menu ul li ul.sub-menu {
  padding: 0;
}
#sidebar-menu ul li ul.sub-menu li a {
  padding: 0.4rem 1.5rem 0.4rem 3.3rem;
  font-size: 14px;
  color: #545a6d;
}
#sidebar-menu ul li ul.sub-menu li a:hover {
  color: #383c40;
}
#sidebar-menu ul li ul.sub-menu li ul.sub-menu {
  padding: 0;
}
#sidebar-menu ul li ul.sub-menu li ul.sub-menu li a {
  padding: 0.4rem 1.5rem 0.4rem 4.5rem;
  font-size: 14px;
}
#sidebar-menu ul.metismenu > li.mm-active > a {
  background-color: #0576b9 !important;
  color: #fff !important;
}
#sidebar-menu ul.metismenu > li.mm-active > a > i {
  color: #fff !important;
}

[dir=rtl] #sidebar-menu .has-arrow:after {
  content: "\ecab";
}

.menu-title {
  padding: 12px 20px !important;
  letter-spacing: 0.05em;
  pointer-events: none;
  cursor: default;
  font-size: 11px;
  text-transform: uppercase;
  color: #545a6d;
  font-weight: 600;
}

.mm-active {
  color: #0576b9 !important;
}
.mm-active > a {
  color: #0576b9 !important;
}
.mm-active > a i {
  color: #0576b9 !important;
}
.mm-active .active {
  color: #0576b9 !important;
}
.mm-active .active i {
  color: #0576b9 !important;
}
.mm-active > i {
  color: #0576b9 !important;
}

@media (max-width: 992px) {
  .vertical-menu {
    display: none;
  }

  .main-content {
    margin-left: 0 !important;
  }

  body.sidebar-enable .vertical-menu {
    display: block;
  }
}
body[data-sidebar-size=sm] .main-content {
  margin-left: 70px;
}
body[data-sidebar-size=sm] .navbar-brand-box {
  width: 70px !important;
}
body[data-sidebar-size=sm] .logo span.logo-lg {
  display: none;
}
body[data-sidebar-size=sm] .logo span.logo-sm {
  display: block;
}
body[data-sidebar-size=sm] .vertical-menu {
  position: absolute;
  width: 70px !important;
  z-index: 5;
}
body[data-sidebar-size=sm] .vertical-menu .simplebar-mask,
body[data-sidebar-size=sm] .vertical-menu .simplebar-content-wrapper {
  overflow: visible !important;
}
body[data-sidebar-size=sm] .vertical-menu .simplebar-scrollbar {
  display: none !important;
}
body[data-sidebar-size=sm] .vertical-menu .simplebar-offset {
